'use client';

import { useEffect, useState } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import { API_URL } from "../api/apiURL";

const WEEK_ORDER = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
const WEEK_LABELS = { Sun:"Su", Mon:"Mo", Tue:"Tu", Wed:"We", Thu:"Th", Fri:"Fr", Sat:"Sa" };

const normalizeDay = (d) => {
  const map = {
    sun:"Sun", mon:"Mon", tue:"Tue", wed:"Wed",
    thu:"Thu", fri:"Fri", sat:"Sat",
    Su:"Sun", Mo:"Mon", Tu:"Tue", We:"Wed",
    Th:"Thu", Fr:"Fri", Sa:"Sat",
  };
  return map[String(d).trim()] || d;
};

const formatK = (num) => (num / 1000).toFixed(2) + " K";

export default function DashboardUserTrend() {
  const [trendData, setTrend] = useState([]);
  const [funnelData, setFunnel] = useState([]);

  /** ---------------- LOAD TREND ---------------- **/
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-signup-trends`);
        const json = await res.json();
        if (!json.success) return;

        const bucket = {};
        json.data.forEach((d) => {
          const day = normalizeDay(d.date);
          bucket[day] = (bucket[day] || 0) + Number(d.newUsers || 0);
        });

        const ordered = WEEK_ORDER.map((d) => ({
          day: d,
          new: bucket[d] || 0,
          returning: Math.floor((bucket[d] || 0) * 0.55),
        }));

        setTrend(ordered);
      } catch (err) {
        console.log(err);
      }
    })();
  }, []);

  /** ---------------- LOAD FUNNEL ---------------- **/
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(`${API_URL}/admin/search-to-match`);
        const json = await res.json();
        if (json.success) setFunnel(json.data);
      } catch (err) {
        console.log(err);
      }
    })();
  }, []);

  /** ---------------- CHART SERIES ---------------- **/
  const newSeries = trendData.map((d) => d.new * 1_000_000);
  const retSeries = trendData.map((d) => d.returning * 1_000_000);

  /** ---------------- HIGHCHART OPTIONS ---------------- **/
  const chartOptions = {
    chart: {
      type: "line",
      height: 340,
      backgroundColor: "#ffffff",
      spacingLeft: 30,
      spacingTop: 10,
      spacingBottom: 20,
    },

    title: { text: "" },
    legend: { enabled: false },

    xAxis: {
      categories: trendData.map((d) => WEEK_LABELS[d.day]),
      tickLength: 0,
      lineWidth: 0,
      labels: {
        style: { fontSize: "18px", fontWeight: 600, color: "#444" },
      },
    },

    yAxis: {
      title: null,
      gridLineColor: "#e5e7eb",
      tickPositions: [0, 5e6, 10e6, 15e6, 20e6, 25e6, 30e6],
      labels: {
        formatter() { return this.value / 1_000_000 + " M"; },
        style: { fontSize: "18px", color: "#666" },
      },
    },

    tooltip: { shared: true },

    plotOptions: {
      series: {
        lineWidth: 4,
        marker: {
          radius: 8,
          fillColor: "#fff",
          lineWidth: 4,
        },
      },
    },

    series: [
      {
        name: "New Registered Users",
        color: "#facc15",
        marker: { lineColor: "#facc15" },
        data: newSeries,
      },
      {
        name: "Returning Users",
        color: "#16a34a",
        marker: { lineColor: "#16a34a" },
        data: retSeries,
      },
    ],
  };

  return (
    <div className="mt-24 px-6 flex flex-col md:flex-row gap-10 w-full">

      {/* LEFT PANEL */}
      <div className="bg-white rounded-[32px] shadow-xl border border-gray-300 p-10 w-full md:w-1/2">

        <h2 className="text-3xl font-bold mb-10">Search To Match</h2>

        <div className="space-y-12">
          {funnelData.map((item, i) => {
            const max = funnelData[0]?.value || 1;
            const pct = (item.value / max) * 100;

            return (
              <div key={i} className="flex items-center justify-between">

                <div className="w-[240px] text-[20px] font-semibold text-gray-800">
                  {item.stage}
                </div>

                <div className="flex-1 mx-4">
                  <div className="w-full h-[32px] bg-gray-200 rounded-full overflow-hidden"
                    style={{
                      boxShadow:
                        "inset 0 2px 4px rgba(0,0,0,0.18), inset 0 -2px 4px rgba(255,255,255,0.4)",
                    }}>
                    <div
                      className="h-full transition-all"
                      style={{
                        width: pct + "%",
                        background: "linear-gradient(90deg,#3A47FF,#001BFF)",
                        borderRadius: "18px",
                        filter: "drop-shadow(0 6px 14px rgba(0,0,0,0.35))",
                      }}
                    ></div>
                  </div>
                </div>

                <div className="w-[120px] text-right text-[20px] font-semibold text-gray-900">
                  {formatK(item.value)}
                </div>

              </div>
            );
          })}
        </div>
      </div>

      {/* RIGHT PANEL */}
      <div className="bg-white rounded-[32px] shadow-xl border border-gray-300 p-10 w-full md:w-1/2">

        {/* ---- Custom Heading + Legend (Outside Highchart) ---- */}
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-3xl font-bold text-gray-900">
            Running User Trend
          </h2>

          <div className="flex items-center gap-10 text-gray-600 text-[18px] font-medium">

            {/* Yellow Legend */}
            <div className="flex items-center gap-2">
              <div className="flex items-center gap-[2px]">
                <span style={{
                  width: 22,
                  height: 4,
                  background: "#facc15",
                  borderRadius: 4
                }}></span>
                <span style={{
                  width: 14,
                  height: 14,
                  borderRadius: "50%",
                  border: "3px solid #facc15",
                  background: "#fff"
                }}></span>
                <span style={{
                  width: 22,
                  height: 4,
                  background: "#facc15",
                  borderRadius: 4
                }}></span>
              </div>
              New Registered Users
            </div>

            {/* Green Legend */}
            <div className="flex items-center gap-2">
              <div className="flex items-center gap-[2px]">
                <span style={{
                  width: 22,
                  height: 4,
                  background: "#16a34a",
                  borderRadius: 4
                }}></span>
                <span style={{
                  width: 14,
                  height: 14,
                  borderRadius: "50%",
                  border: "3px solid #16a34a",
                  background: "#fff"
                }}></span>
                <span style={{
                  width: 22,
                  height: 4,
                  background: "#16a34a",
                  borderRadius: 4
                }}></span>
              </div>
              Returning Users
            </div>

          </div>
        </div>

        <HighchartsReact highcharts={Highcharts} options={chartOptions} />

      </div>
    </div>
  );
}
