"use client";

import React, { useEffect, useState } from "react";
import dynamic from "next/dynamic";
import Highcharts from "highcharts";
import axios from "axios";

// dynamic import so HighchartsReact only runs on client
const HighchartsReact = dynamic(() => import("highcharts-react-official"), {
  ssr: false,
});

// Reference images you uploaded (toolchain will map these local paths to URLs)
const REF_PIE = "/mnt/data/Screenshot 2025-11-25 151215.png";
const REF_MATCH = "/mnt/data/Screenshot 2025-11-25 151230.png";
const REF_FULL = "/mnt/data/Screenshot 2025-11-25 151241.png";

export default function ProfileMatch() {
  const months = [
    "Jan","Feb","Mar","Apr","May","Jun",
    "Jul","Aug","Sep","Oct","Nov","Dec"
  ];

  const [overview, setOverview] = useState([]);
  const [monthly, setMonthly] = useState([]);

  // safe client-only loader for highcharts-3d (handles mod.default or mod)
  useEffect(() => {
    (async () => {
      if (typeof window === "undefined") return;
      try {
        const mod = await import("highcharts/highcharts-3d");
        const loader = mod && (mod.default || mod);
        if (typeof loader === "function") loader(Highcharts);
      } catch (err) {
        // if plugin fails to load, we still continue without 3d
        console.warn("highcharts-3d load failed:", err);
      }
    })();
  }, []);

  // fetch data
  useEffect(() => {
    (async () => {
      try {
        const [ovRes, mRes] = await Promise.all([
          axios.get("{{matrimonial}}/admin/overview"),
          axios.get("{{matrimonial}}/admin/matches-per-month"),
        ]);

        if (ovRes?.data?.success) setOverview(ovRes.data.data || []);
        if (mRes?.data?.success) setMonthly(mRes.data.data || []);
      } catch (err) {
        console.error("API fetch error:", err);
      }
    })();
  }, []);

  const colors = {
    Completed: "#0c7c1b",
    Low: "#f6931d",
    Incomplete: "#e21d1d",
    Moderate: "#f6c927",
  };

  // PIE options (3D if plugin available)
  const pieOptions = {
    chart: {
      type: "pie",
      backgroundColor: "transparent",
      options3d: { enabled: true, alpha: 45 },
      height: 340,
    },
    title: { text: "" },
    credits: { enabled: false },
    plotOptions: {
      pie: {
        depth: 45,
        borderWidth: 8,
        borderColor: "#fff",
        dataLabels: { enabled: false },
        shadow: {
          color: "rgba(0,0,0,0.28)",
          offsetX: 0,
          offsetY: 12,
          width: 22,
          opacity: 0.8,
        },
        slicedOffset: 8,
        startAngle: -30,
      },
    },
    tooltip: {
      useHTML: true,
      formatter() {
        const total = this.series.data.reduce((s, p) => s + (p.y || 0), 0) || 1;
        const pct = ((this.point.y / total) * 100).toFixed(1);
        return `<div style="font-weight:700;color:#222">${this.point.name}</div>
                <div style="margin-top:4px">${this.point.y} Users &nbsp; (<span style="font-weight:700">${pct}%</span>)</div>`;
      },
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
    },
    series: [
      {
        type: "pie",
        data: (["Completed","Low","Incomplete","Moderate"]).map((name) => {
          const row = overview.find((r) => r.name === name);
          return { name, y: row ? row.value : 0, color: colors[name] };
        }),
      },
    ],
  };

  // MATCHES options (column + line)
  const matchOptions = {
    chart: { backgroundColor: "transparent", height: 420 },
    title: { text: "" },
    credits: { enabled: false },
    xAxis: {
      categories: months,
      labels: { style: { fontSize: 14, fontWeight: 600, color: "#444" } },
      lineColor: "#eee",
    },
    yAxis: {
      min: 0,
      max: 35,
      tickInterval: 5,
      gridLineColor: "#e6e6e6",
      labels: { formatter() { return this.value + " M"; }, style: { fontWeight: 600, color: "#666" } },
      title: { text: "" },
    },
    tooltip: {
      shared: true,
      useHTML: true,
      formatter() {
        let html = `<div style="font-weight:700;margin-bottom:6px">${this.x}</div>`;
        this.points.forEach((pt) => {
          html += `<div style="margin:4px 0">
                    <span style="display:inline-block;width:10px;height:10px;background:${pt.color};border-radius:2px;margin-right:8px;vertical-align:middle"></span>
                    <strong style="font-weight:600">${pt.series.name}:</strong>
                    &nbsp; ${pt.y}
                   </div>`;
        });
        return html;
      },
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
    },
    plotOptions: {
      column: {
        borderRadius: 24,
        pointPadding: 0.12,
        borderWidth: 0,
        grouping: true,
        shadow: { color: "rgba(0,0,0,0.28)", offsetX: 0, offsetY: 5, width: 12 },
      },
      series: { states: { hover: { enabled: true, brightness: 0.03 } } },
    },
    series: [
      {
        type: "column",
        name: "Total no. of Users",
        data: months.map((m) => (monthly.find((r) => r.month === m)?.totalUsers ?? 0)),
        color: {
          linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
          stops: [
            [0, "#f7d34a"],
            [1, "#d7a304"],
          ],
        },
      },
      {
        type: "line",
        name: "No. Of Matches",
        data: months.map((m) => (monthly.find((r) => r.month === m)?.matches ?? 0)),
        color: "#e52b2b",
        lineWidth: 4,
        marker: { enabled: true, radius: 7, fillColor: "#fff", lineColor: "#e52b2b", lineWidth: 2 },
      },
    ],
  };

  const legendBoxStyle = (color) => ({
    width: 16,
    height: 16,
    borderRadius: 4,
    background: "#fff",
    border: `2.5px solid ${color}`,
    boxSizing: "border-box",
  });

  return (
    <div style={{ width: "100%", padding: 20 }}>
      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 30 }}>
        {/* PROFILE OVERVIEW */}
        <div style={{ background: "#fff", borderRadius: 18, padding: 24, border: "2px solid rgba(0,0,0,0.08)", boxShadow: "0 6px 18px rgba(0,0,0,0.06)", position: "relative", minHeight: 360 }}>
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start" }}>
            <h3 style={{ fontSize: 26, fontWeight: 700, margin: 0 }}>Profile Overview</h3>
            {/* custom vertical legend */}
            <div style={{ display: "flex", flexDirection: "column", gap: 12, marginLeft: 12, marginTop: 2 }}>
              <div style={{ display: "flex", alignItems: "center", gap: 10, fontSize: 17, fontWeight: 600 }}>
                <div style={legendBoxStyle(colors.Completed)} />
                <span>Completed</span>
              </div>
              <div style={{ display: "flex", alignItems: "center", gap: 10, fontSize: 17, fontWeight: 600 }}>
                <div style={legendBoxStyle(colors.Low)} />
                <span>Low</span>
              </div>
              <div style={{ display: "flex", alignItems: "center", gap: 10, fontSize: 17, fontWeight: 600 }}>
                <div style={legendBoxStyle(colors.Incomplete)} />
                <span>Incomplete</span>
              </div>
              <div style={{ display: "flex", alignItems: "center", gap: 10, fontSize: 17, fontWeight: 600 }}>
                <div style={legendBoxStyle(colors.Moderate)} />
                <span>Moderate</span>
              </div>
            </div>
          </div>

          <div style={{ marginTop: 36 }}>
            <HighchartsReact highcharts={Highcharts} options={pieOptions} />
          </div>
        </div>

        {/* MATCHES PER MONTH */}
        <div style={{ background: "#fff", borderRadius: 18, padding: 24, border: "2px solid rgba(0,0,0,0.08)", boxShadow: "0 6px 18px rgba(0,0,0,0.06)", position: "relative", minHeight: 520 }}>
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start" }}>
            <h3 style={{ fontSize: 26, fontWeight: 700, margin: 0 }}>Matches Per Month</h3>

            {/* custom horizontal legend aligned with title (single row) */}
            <div style={{ display: "flex", gap: 48, alignItems: "center", marginTop: 2 }}>
              <div style={{ display: "flex", alignItems: "center", gap: 10, fontSize: 16, fontWeight: 600 }}>
                <div style={legendBoxStyle("#d7a304")} />
                <span>Total no. of Users</span>
              </div>

              <div style={{ display: "flex", alignItems: "center", gap: 10, fontSize: 16, fontWeight: 600 }}>
                <div style={legendBoxStyle("#e52b2b")} />
                <span>No. Of Matches</span>
              </div>
            </div>
          </div>

          <div style={{ marginTop: 28 }}>
            <HighchartsReact highcharts={Highcharts} options={matchOptions} />
          </div>
        </div>
      </div>

      {/* optional reference images (for developer debugging) */}
      {/* <div style={{ display: "none" }}>
        <img src={REF_PIE} alt="pie ref" />
        <img src={REF_MATCH} alt="match ref" />
        <img src={REF_FULL} alt="full ref" />
      </div> */}
    </div>
  );
}
